import { v1 as uuid } from 'uuid'

const CODELIST = [{ id:'1', name:'TEH' }]

export interface ICode {
    id: string
    name: string
    url?: string
    imageUrl?: string
}

export interface ICodelistQuery {
    id?: string
    //primary?: boolean
}

export async function fetchCodelist(query:ICodelistQuery): Promise<ICode[]> {
    let codelist: ICode[]
    if (localStorage.getItem('codelist')) {
        codelist = JSON.parse(localStorage.getItem('codelist')!)
    } else {
        codelist = CODELIST
    }
    if (query) {
        if (query.id !== undefined) {
            codelist = codelist.filter((code) => code.id === query.id)
        }
        //if (query.primary !== undefined) {
        //    codelist = codelist.filter((code) => code.primary === query.primary)
        //}
    }
    return codelist
}

export async function fetchCode(id: string): Promise<ICode|null> {
    const [ code ] = await fetchCodelist({ id })
    return code || null
}

export async function createCode(data: Partial<ICode>) {
    if (!data.name) {
        throw Error()
    }
    const code: ICode = {
        ...data,
        id: uuid(),
        name: data.name,
    }
    const codelist: ICode[] = localStorage.getItem('codelist') ? JSON.parse(localStorage.getItem('codelist')!) : []
    codelist.push(code)
    localStorage.setItem('codelist', JSON.stringify(codelist))
    return code
}

export async function updateCode(id: string, data: Partial<ICode>) {
    if (!data.name) {
        throw Error()
    }
    const code: ICode = {
        ...data,
        id,
        name: data.name,
    }
    let codelist: ICode[] = localStorage.getItem('codelist') ? JSON.parse(localStorage.getItem('codelist')!) : []
    codelist = codelist.map((existsCode) => existsCode.id !== id ? existsCode : code)
    localStorage.setItem('codelist', JSON.stringify(codelist))
    return code
}

export async function deleteCode(id: string) {
    let codelist: ICode[] = localStorage.getItem('codelist') ? JSON.parse(localStorage.getItem('codelist')!) : []
    codelist = codelist.filter((code) => code.id !== id)
    localStorage.setItem('codelist', JSON.stringify(codelist))
}

export async function fetchCurrentCodeId() {
    return localStorage.getItem('currentCodeId')
}

export async function putCurrentCodeId(id: string) {
    localStorage.setItem('currentCodeId', id)
}
