import React from 'react'
import makeStyles from '@mui/styles/makeStyles'
import styles from './CodelistItem.styles'

import { ICode } from '../../../codes/services/CodesService'
import { useHistory, useRouteMatch } from 'react-router-dom'

import { NavLink } from 'react-router-dom'
import ListItem from '@mui/material/ListItem'
import ListItemButton from '@mui/material/ListItemButton'
import ListItemText from '@mui/material/ListItemText'
import IconButton from '@mui/material/IconButton'
import MoreVertIcon from '@mui/icons-material/MoreVert'
import Menu from '@mui/material/Menu'
import MenuItem from '@mui/material/MenuItem'

const useStyles = makeStyles(styles, {
    name: CodelistItem.name,
})

export interface ICodelistItemProps {
    item: ICode
}

export default function CodelistItem(props: ICodelistItemProps) {
    const classes = useStyles(props)

    const { item } = props

    const [ menuOpen, setMenuOpen ] = React.useState(false)
    const menuAnchorRef = React.useRef<HTMLButtonElement>(null)

    const routeMatch = useRouteMatch()

    return (
        <>

            <ListItem
                disablePadding
                secondaryAction={(
                    <IconButton
                        ref={menuAnchorRef}
                        aria-label="comments"
                        edge="end"
                        onClick={() => setMenuOpen(true)}
                    >
                        <MoreVertIcon/>
                    </IconButton>
                )}
            >
                <ListItemButton
                    component={NavLink}
                    to={`/codes/code/${item.id}`}
                >
                    <ListItemText primary={item.name || item.id} />
                </ListItemButton>
            </ListItem>

            {menuAnchorRef.current && (
                <Menu
                    anchorEl={menuAnchorRef.current}
                    open={menuOpen}
                    onClose={() => setMenuOpen(false)}
                >

                    <MenuItem
                        component={NavLink}
                        to={`${routeMatch.path}/${item.id}/update`}
                        onClick={() => setMenuOpen(false)}
                    >
                        ИЗМЕНИТЬ
                    </MenuItem>

                    <MenuItem
                        component={NavLink}
                        to={`${routeMatch.path}/${item.id}/delete`}
                        onClick={() => setMenuOpen(false)}
                    >
                        УДАЛИТЬ
                    </MenuItem>

                </Menu>
            )}

        </>
    )
}
