import React from 'react'

import { useParams } from 'react-router-dom'
import { fetchCode, updateCode, ICode } from '../../../../codes/services/CodesService'

import Dialog from '@mui/material/Dialog'
import AppBar from '@mui/material/AppBar'
import Toolbar from '@mui/material/Toolbar'
import Container from '@mui/material/Container'
import CodelistItemForm from '../../CodelistItemForm/CodelistItemForm'
import Button from '@mui/material/Button'

export interface IUpdateItemProps {
    onClose: () => void
    onSuccess: (code: ICode) => void
}

export default function UpdateItem(props: IUpdateItemProps) {
    const { onClose, onSuccess } = props

    const { id } = useParams<{ id:string }>()

    const [ code, setCode ] = React.useState<Partial<ICode> | null>()
    const [ codePending, setCodePending ] = React.useState(true)

    React.useEffect(() => {
        setCodePending(true)
        fetchCode(id)
            .then((code) => {
                setCode(code)
                setCodePending(false)
            })
        ;
    }, [id])

    const [ submitPending, setSubmitPending ] = React.useState(false)

    const handleSubmit = () => {
        if (!code) {
            return
        }
        setSubmitPending(true)
        updateCode(id, code)
            .then((code) => {
                onSuccess(code)
            })
        ;
    }

    return (
        <Dialog
            open
            onClose={onClose}
            fullScreen
        >
            <AppBar position="sticky" color="default">
                <Toolbar>
                    -
                </Toolbar>
            </AppBar>

            {code && (
                <Container maxWidth="md">

                    <CodelistItemForm
                        data={code}
                        onChangeData={setCode}
                        disabled={submitPending}
                    />

                    <Button
                        variant="contained"
                        disabled={submitPending}
                        onClick={handleSubmit}
                    >
                        Сохранить
                    </Button>

                </Container>
            )}

        </Dialog>
    )
}
