import React from 'react'
import makeStyles from '@mui/styles/makeStyles'
import styles from './Codes.styles'

import { fetchCurrentCodeId, ICode } from '../../services/CodesService'

import { Switch, Route, Redirect } from 'react-router-dom'

import Code from '../../../codes-code/components/Code/Code'
import Codelist from '../../../codes-codelist/components/Codelist/Codelist'

const useStyles = makeStyles(styles, {
    name: Codes.name,
})

export default function Codes() {
    const classes = useStyles()

    const [ currentCodeId, setCurrentCodeId ] = React.useState<string | null>(null)
    const [ currentCodeIdPending, setCurrentCodeIdPending ] = React.useState(true)

    React.useEffect(() => {
        fetchCurrentCodeId()
            .then((currentCodeId) => {
                setCurrentCodeId(currentCodeId)
                setCurrentCodeIdPending(false)
            })
        ;
    }, [])

    return (
        <>

            {!currentCodeIdPending && (
                <Switch>

                    <Route path="/codes/codelist" component={Codelist}/>

                    <Route path="/codes/code/:id" component={Code}/>

                    {currentCodeId ? (
                        <Redirect to={`/codes/code/${currentCodeId}`}/>
                    ) : (
                        <Redirect to={`/codes/codelist`}/>
                    )}

                </Switch>
            )}

        </>
    )
}
