import React from 'react'
import makeStyles from '@mui/styles/makeStyles'
import styles from './Codelist.styles'

import { fetchCodelist, ICode } from '../../../codes/services/CodesService'
import { useHistory, useRouteMatch } from 'react-router-dom'

import AppBar from '@mui/material/AppBar'
import Container from '@mui/material/Container'
import Toolbar from '@mui/material/Toolbar'
import LinearProgress from '@mui/material/LinearProgress'
import List from '@mui/material/List'
import CodelistItem from '../CodelistItem/CodelistItem'
import Fab from '@mui/material/Fab'
import AddIcon from '@mui/icons-material/Add'

import CreateItem from '../CodelistTool/CreateItem/CreateItem'
import UpdateItem from '../CodelistTool/UpdateItem/UpdateItem'
import DeleteItem from '../CodelistTool/DeleteItem/DeleteItem'

import { Switch, Route, NavLink } from 'react-router-dom'

const useStyles = makeStyles(styles, {
    name: Codelist.name,
})

export default function Codelist() {
    const classes = useStyles()

    const [ codelist, setCodelist ] = React.useState<ICode[] | null>(null)
    const [ codelistPending, setCodelistPending ] = React.useState(true)

    React.useEffect(() => {
        if (!codelistPending) {
            return
        }
        let active = true
        fetchCodelist({})
            .then((codelist) => {
                if (active) {
                    setCodelist(codelist)
                    setCodelistPending(false)
                }
            })
        ;
        return () => {
            active = false
        }
    }, [codelistPending])

    const history = useHistory()
    const routeMatch = useRouteMatch()

    return (
        <>

            <AppBar position="sticky">
                <Container maxWidth={false}>
                    <Toolbar disableGutters>
                        COVIDCODE
                    </Toolbar>
                </Container>
            </AppBar>

            {codelistPending && (
                <div className={classes.progressContainer}>
                    <LinearProgress className={classes.progress} color="secondary"/>
                </div>
            )}

            <div className={classes.main}>
                {codelist && (
                    <List>
                        {codelist.map((item) => (
                            <CodelistItem
                                key={item.id}
                                item={item}
                            />
                        ))}
                    </List>
                )}
            </div>

            <div className={classes.foot}>
                <Fab
                    color="primary"
                    component={NavLink}
                    to={`${routeMatch.url}/create`}
                >
                    <AddIcon/>
                </Fab>
            </div>

            <Switch>

                <Route path={`${routeMatch.path}/create`}>
                    <CreateItem
                        onClose={() => {
                            history.goBack()
                        }}
                        onSuccess={() => {
                            history.goBack()
                            setCodelistPending(true)
                        }}
                    />
                </Route>

                <Route path={`${routeMatch.path}/:id/update`}>
                    <UpdateItem
                        onClose={() => {
                            history.goBack()
                        }}
                        onSuccess={() => {
                            history.goBack()
                            setCodelistPending(true)
                        }}
                    />
                </Route>

                <Route path={`${routeMatch.path}/:id/delete`}>
                    <DeleteItem
                        onClose={() => {
                            history.goBack()
                        }}
                        onSuccess={() => {
                            history.goBack()
                            setCodelistPending(true)
                        }}
                    />
                </Route>

            </Switch>

        </>
    )
}
