import React from 'react'

import { ICode } from '../../../codes/services/CodesService'

import TextField from '@mui/material/TextField'
import FileField from '../../../../components/FileField/FileField'

export interface ICodelistItemFormProps {
    data: Partial<ICode>
    onChangeData: (data: Partial<ICode>) => void
    disabled?: boolean
}

export default function CodelistItemForm(props: ICodelistItemFormProps) {
    const { data, onChangeData, disabled } = props

    return (
        <form>

            <TextField
                label="Название (чем короче, тем лучше)"
                name="name"
                variant="outlined"
                margin="normal"
                fullWidth
                disabled={disabled}
                value={data.name || ''}
                onChange={({ target:{ value }}) => {
                    onChangeData({ ...data, name:value })
                }}
            />

            <TextField
                label="Адрес страницы на Госуслугах"
                name="url"
                variant="outlined"
                margin="normal"
                fullWidth
                disabled={disabled}
                value={data.url || ''}
                onChange={({ target:{ value }}) => {
                    onChangeData({ ...data, url:value })
                }}
            />

            <FileField
                label="Адрес изображения"
                name="url"
                variant="outlined"
                margin="normal"
                fullWidth
                disabled={disabled}
                value={data.imageUrl || ''}
                onChangeValue={(value) => {
                    onChangeData({ ...data, imageUrl:value })
                }}
            />

        </form>
    )
}
