import { createStyles } from '@mui/styles'
import { Theme } from '@mui/material'

const styles = (theme: Theme) => createStyles({

    root: {

    },

    progressContainer: {
        position: 'relative',
    },
    progress: {
        position: 'absolute',
        width: '100%',
    },

    main: {
        flexGrow: 1,
        display: 'flex',
        flexDirection: 'column',
    },

    foot: {
        pointerEvents: 'none',
        position: 'sticky',
        bottom: 0,
        display: 'flex',
        justifyContent: 'flex-end',
        padding: 32,
        '& > *': {
            pointerEvents: 'all',
        }
    },

})

export default styles
